body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.catalog__header {
  box-sizing: border-box;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding-inline: 50px;
  display: flex;
}

.header .nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.header .nav__item:not(:last-child) {
  margin-right: 20px;
}

.header .nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
  position: relative;
}

.header .nav__link:hover, .header .nav .is-active {
  color: #00acdc;
}

.header .nav .is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.catalog__card {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 300px) {
  .catalog__card {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 488px) {
  .catalog__card {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog__card {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog__card {
    grid-template-columns: repeat(4, 200px);
  }
}

.catalog__card .card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  max-width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: flex;
}

.catalog__card .card__image {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
  display: block;
}

.catalog__card .card__content {
  text-align: center;
}

.catalog__card .card__content-title {
  color: #060b35;
  text-align: left;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.catalog__card .card:hover {
  transform: scale(1.2);
}

.catalog__card .card:hover .card__content-title {
  color: #34568b;
}

.catalog__card .card__content-description {
  color: #616070;
  text-align: left;
  margin-top: 4px;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.catalog__card .card__content-reviews {
  color: #060b35;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 24px;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.catalog__card .card__content-price {
  color: #616070;
  justify-content: space-between;
  padding-bottom: 16px;
  font-size: 12px;
  line-height: 18px;
  display: flex;
}

.catalog__card .card__content-price .price {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.catalog__card .card__button {
  cursor: pointer;
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 166px;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition-duration: .3s;
}

.catalog__card .card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  height: 40px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
